.Graph {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.labelinput {
  align-self: flex-start;
  font-size: 1.5rem;
}

.PreselectedAndInvestBox {
  display: flex;
  width: 50%;
  margin-top: 3.2rem;
}

.PreselectedBox {
  width: 50%;
  display: flex;
  flex-direction: column;
  padding-right: 4.8rem;
  font-size: 1.7rem;
}

.PreselectedLine {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #999;
  margin-bottom: 0.8rem;
}

.delbtn {
  background-color: red;
  font-weight: bold;
  width: 1.6rem;
  color: white;
  cursor: pointer;
  border-radius: 2px;
  transition: 0.2s;
}

.delbtn:hover {
  background-color: orangered;
}

.InvestBox {
  width: 50%;
  display: flex;
  flex-direction: column;
  height: 16rem;
  align-items: flex-start;
  padding-left: 4.8rem;
}

.dollarAndInput {
  display: flex;
  align-items: center;
}

#simulatewithinput {
  background-color: #2f3640;
  border: none;
  color: #ccc;
  font-size: 4.8rem;
  width: 15rem;
  height: 8rem;
}

#simulatewithinput:focus {
  outline: none;
}

#simulatewithinput::-webkit-outer-spin-button,
#simulatewithinput::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

#simulatewithinput[type='number'] {
  -moz-appearance: textfield;
}

.dollar {
  font-size: 4.8rem;
}

.DatePickers {
  display: flex;
}

.StartDateBox,
.EndDateBox {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 0.8rem;
}

.analyzebtn {
  margin-top: 2.4rem;
  height: 2.88rem;
  width: 8rem;
  background-color: black;
  color: #00a8ff;
  border: 1px solid #00a8ff;
  cursor: pointer;
  transition: 0.2s;
}

.analyzebtn:hover {
  transform: translate(-2px, -2px);
}

.analyzebtn:active {
  transform: translate(0, 0);
}

.analyzebtn:disabled,
.analyzebtn[disabled] {
  color: #ccc;
  border: 1px solid #ccc;
  cursor: auto;
}

.analyzebtn:disabled:hover,
.analyzebtn[disabled]:hover {
  transform: translate(0, 0);
}

.ChartBox {
  margin-top: 2rem;
}

.GraphText {
  width: 80%;
  line-height: 2.4rem;
  margin-bottom: 3.2rem;
  font-size: 1.8rem;
}

.resultsBox {
  display: flex;
  justify-content: center;
}

.liresult {
  margin-bottom: 1rem;
}

.FromTo {
  margin: 1rem;
}

.date {
  font-weight: bold;
  color: #00a8ff;
}

.unordered {
  text-align: justify;
}

.SuggestionsBox {
  width: 250%;
  background-color: black;
  color: #00a8ff;
  display: flex;
  flex-direction: column;
  font-size: 1.6rem;
}

.SuggestionsEach {
  display: flex;
  justify-content: space-between;
  padding: 0.64rem 0.8rem;
  border-bottom: 1px solid #00a8ff;
  cursor: pointer;
}

.SuggestionsEach:hover {
  background-color: #333;
}

.gettingdata {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}

.innergettingdata {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: #222;
  border-radius: 10px;
  height: 50vh;
  width: 80%;
}

.fetching {
  color: #00a8ff;
  font-weight: bold;
}

.thankyoudiv {
  height: 50vh;
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  font-size: 1.7rem;
  line-height: 24px;
}

@media only screen and (max-width: 1000px) {
  .PreselectedAndInvestBox {
    width: 60%;
  }
}

@media only screen and (max-width: 800px) {
  .PreselectedAndInvestBox {
    width: 75%;
  }

  .GraphText {
    width: 100%;
    line-height: 2.8rem;
    margin-bottom: 3.2rem;
    font-size: 1.9rem;
  }

  .textpar {
    margin: 0rem 2rem;
  }

  .liresult {
    margin: 0 4rem 1rem;
  }

  .FromTo {
    margin: 2rem;
  }

  .thankyoudiv {
    width: 90%;
    font-size: 1.8rem;
  }
}

@media only screen and (max-width: 600px) {
  .PreselectedAndInvestBox {
    width: 85%;
  }

  .thankyouh1 {
    line-height: 4rem;
  }
}

@media only screen and (max-width: 500px) {
  .PreselectedAndInvestBox {
    width: 88%;
  }

  #simulatewithinput {
    font-size: 2rem;
    width: 10rem;
    height: 8rem;
  }

  .dollar {
    font-size: 2rem;
  }

  .StartDateBox,
  .EndDateBox {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0.8rem;
  }

  #EndDate,
  #StartDate {
    width: 12rem;
  }

  .SuggestionsBox {
    width: 180%;
    background-color: black;
    color: #00a8ff;
    display: flex;
    flex-direction: column;
    font-size: 1.3rem;
  }

  .SuggestionsEach {
    display: flex;
    justify-content: space-between;
    padding: 0.64rem 0.8rem;
    border-bottom: 1px solid #00a8ff;
    cursor: pointer;
  }
}

@media only screen and (max-width: 400px) {
  .PreselectedAndInvestBox {
    width: 90%;
  }
  .thankyoudiv {
    height: 80vh;
  }
}
