.Welcome {
  text-align: center;
  width: 80%;
  padding: 3.2rem;
  font-size: 1.8rem;
}

.WelcFirst {
  font-size: 2.8rem;
  margin: 3.2rem;
}

.Welcome ol li {
  text-align: justify;
  width: 100%;
  margin: 1.6rem;
}

.NoteThat {
  margin-top: 3.2rem;
  font-size: 1.6rem;
}

@media only screen and (max-width: 800px) {
  .Welcome {
    text-align: justify;
    width: 100%;
    padding: 3.2rem;
    font-size: 1.8rem;
  }

  .WelcFirst {
    font-size: 2.8rem;
    margin: 3rem;
  }

  .Welcome ol li {
    text-align: justify;
    width: 90%;
    margin: 1.6rem;
  }

  .NoteThat {
    margin-top: 3.2rem;
    font-size: 1.6rem;
  }
}
