* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  background-color: #2f3640;
  color: #ccc;
  display: flex;
  flex-direction: column;
}

.Footer {
  background-color: #273c75;
  width: 100%;
}

.portfoliobtn {
  text-decoration: none;
  padding-top: 0.6rem;
  padding-left: 0.5rem;
  margin-top: 2.2rem;
  margin-left: 2rem;
  height: 2.8rem;
  width: 13rem;
  background-color: black;
  color: #00a8ff;
  border: 1px solid #00a8ff;
  cursor: pointer;
  transition: 0.2s;
  font-size: 1.4rem;
  position: fixed;
  z-index: 500000;
}

.portfoliobtn:hover {
  transform: translate(-2px, -2px);
}

.portfoliobtn:active {
  transform: translate(0, 0);
}

@media only screen and (max-width: 800px) {
  .portfoliobtn {
    display: none;
  }
}
